'use strict';

import "./MarkupsUI/UIComponents.css";
import getUIClasses from './MarkupsUI';
import { locales } from './Locale';

const av = Autodesk.Viewing;
const CORE_EXTENSION = 'Autodesk.BIM360.Markups';
const namespace = AutodeskNamespace(CORE_EXTENSION);

export class VaultMarkupsExtension extends av.Extension {
    
    constructor(viewer, options) {
        super(viewer, options);
        this.modes = [];
        this.mode = '';
        this.name = 'VaultMarkups';
    }

    async load() {
        super.load();
        this.extendLocalization(locales);
        this.core = await this.viewer.loadExtension(CORE_EXTENSION, {uiClasses : getUIClasses()});
        this.core.markupsManager.setToolEnabled(false);
        this.registerEventListeners();
        return true;
    }

    async onToolbarCreated(toolbar) {
        super.onToolbarCreated(toolbar);

        const markupToolButton = new Autodesk.Viewing.UI.Button("toolbar-markup-tool");
        markupToolButton.setToolTip("Markup");
        markupToolButton.setIcon("v-icon-markup");
        markupToolButton.onClick = () => {
            this.activate();
        };

        const modelTools = toolbar.getControl(Autodesk.Viewing.TOOLBAR.MODELTOOLSID);
        if (modelTools) {
            modelTools.addControl(markupToolButton, {index:0});
        }
        this.markupToolButton = markupToolButton;
    }

    registerEventListeners() {
        this.onEndMarkupSession = this.onEndMarkupSession.bind(this);
        this.core.markupsManager.addEventListener(
			namespace.Events.MARKUP_SESSION_END,
			this.onEndMarkupSession
		);
	}

	unregisterEventListeners() {
        this.core.markupsManager.removeEventListener(
			namespace.Events.MARKUP_SESSION_END,
			this.onEndMarkupSession
		);
	}
    
    onEndMarkupSession() {
        this.deactivate();
    }

    activate(mode) {
        super.activate(mode);
        if(!this.activeStatus) {
            this.activeStatus = true;
            this.viewer.toolbar.setVisible(false);
            this.core.markupsManager.setToolEnabled(true);
            this.core.markupsManager.startMarkupSession();
        }
        return true;
    }

    deactivate() {
        super.deactivate();
        if(this.activeStatus) {
            this.activeStatus = false;
            this.viewer.toolbar.setVisible(true);
            this.core.markupsManager.setToolEnabled(false);

        }
        return true;
    }
}

av.theExtensionManager.registerExtension('Autodesk.Vault.Markups', VaultMarkupsExtension);
