import { html, render, MarkupsContextualToolbar } from "./UIComponents";

const namespace = AutodeskNamespace('Autodesk.BIM360.Markups');
const HISTORY_CHANGED_EVENT = 'action-manager.history.changed.event';

const GeometryUtils = {
    applyRotation: (points, angle, pivot, frontVector) => {
        if (!(points instanceof Array)) {
            points = [points];
        }

        points.forEach(point => {
            point.sub(pivot);
            point.applyAxisAngle(frontVector, angle);
            point.add(pivot);
        });
    }
};

const ToolbarLocation = {
    TOP: 'top',
    BOTTOM: 'bottom'
};
export default class MarkupsContextualMenu {
    constructor(markupsExtension, uiContainer, viewer, options) {
        this.markupsExtension = markupsExtension;
        this.viewer = viewer;
        this.uiContainer = uiContainer;
        this.options = options;
        this.DEFAULT_FIRST_OFFSET = 250;

        this.disableDecrease = false;
        this.disableIncrease = false;
        this.subLeft = 0;
        this.init();
    }

    init() {
        this.menuContainer = document.createElement('div');
        this.menuContainer.className = 'markup-ui-contextual-menu-container';
        this.uiContainer.appendChild(this.menuContainer);
        this.registerEventListeners();
        this.render();
    }

    registerEventListeners() {
        this.onSelectionChangedBinded = this.onSelectionChanged.bind(this);
        this.onCameraChangedBinded = this.onCameraChanged.bind(this);
        this.hideMenuBinded = this.hideMenu.bind(this);
        this.onClickOutsideBinded = this.onClickOutside.bind(this);
        this.onMarkupDirtyBinded = this.onMarkupDirty.bind(this);

        this.markupsExtension.markupsManager.addEventListener(
            HISTORY_CHANGED_EVENT,
            this.onSelectionChangedBinded
        );
        this.markupsExtension.markupsManager.addEventListener(
            namespace.Events.MARKUP_SELECTED,
            this.onSelectionChangedBinded
        );
        this.markupsExtension.markupsManager.addEventListener(
            namespace.Events.MARKUP_DESELECTED,
            this.onSelectionChangedBinded
        );
        this.markupsExtension.markupsManager.addEventListener(
            namespace.Events.LAYER_DIRTY,
            this.onSelectionChangedBinded
        );
        this.markupsExtension.markupsManager.addEventListener(
            namespace.Events.LAYER_SYNCED,
            this.onSelectionChangedBinded
        );
        this.markupsExtension.markupsManager.addEventListener(
            namespace.Events.LAYER_SELECTED,
            this.onSelectionChangedBinded
        );
        this.markupsExtension.markupsManager.addEventListener(namespace.Events.LAYER_DESELECTED, this.hideMenuBinded);
        this.markupsExtension.markupsManager.addEventListener(namespace.Events.BUTTON_DOWN, this.onClickOutsideBinded);
        this.markupsExtension.markupsManager.addEventListener(namespace.Events.MARKUP_EDIT_START, this.hideMenuBinded);
        this.markupsExtension.markupsManager.addEventListener(
            namespace.Events.MARKUP_EDIT_END,
            this.onMarkupDirtyBinded
        );
        this.markupsExtension.markupsManager.addEventListener(
            namespace.Events.MARKUP_TOOL_ENABLED_CHANGED,
            this.onSelectionChangedBinded
        );
        this.viewer.addEventListener(Autodesk.Viewing.CAMERA_CHANGE_EVENT, this.onCameraChangedBinded);
    }

    unregisterEventListeners() {
        this.markupsExtension.markupsManager.removeEventListener(
            HISTORY_CHANGED_EVENT,
            this.onSelectionChangedBinded
        );
        this.markupsExtension.markupsManager.removeEventListener(
            namespace.Events.MARKUP_SELECTED,
            this.onSelectionChangedBinded
        );
        this.markupsExtension.markupsManager.removeEventListener(
            namespace.Events.MARKUP_DESELECTED,
            this.onSelectionChangedBinded
        );
        this.markupsExtension.markupsManager.removeEventListener(
            namespace.Events.LAYER_DIRTY,
            this.onSelectionChangedBinded
        );
        this.markupsExtension.markupsManager.removeEventListener(
            namespace.Events.LAYER_SYNCED,
            this.onSelectionChangedBinded
        );
        this.markupsExtension.markupsManager.removeEventListener(
            namespace.Events.LAYER_SELECTED,
            this.onSelectionChangedBinded
        );
        this.markupsExtension.markupsManager.removeEventListener(
            namespace.Events.LAYER_DESELECTED,
            this.hideMenuBinded
        );
        this.markupsExtension.markupsManager.removeEventListener(
            namespace.Events.MARKUP_EDIT_START,
            this.hideMenuBinded
        );
        this.markupsExtension.markupsManager.removeEventListener(
            namespace.Events.MARKUP_EDIT_END,
            this.onMarkupDirtyBinded
        );
        this.markupsExtension.markupsManager.removeEventListener(
            namespace.Events.BUTTON_DOWN,
            this.onClickOutsideBinded
        );
        this.markupsExtension.markupsManager.removeEventListener(
            namespace.Events.MARKUP_TOOL_ENABLED_CHANGED,
            this.onSelectionChangedBinded
        );
        this.viewer.removeEventListener(Autodesk.Viewing.CAMERA_CHANGE_EVENT, this.onCameraChangedBinded);
    }

    onSelectionChanged(event) {
        if (event && event.actionGroup && event.actionGroup[0] && event.actionGroup[0].isDuringEdit) {
            return;
        }
        this.position = null;
        this.boundingBox = null;
        this.selectedMarkups = [];
        this.selectedLayer = null;

        this.locatePosition();
        this.setFontSizeButtonStatus();
        this.locateSubMenu();
        this.render();
    }

    onClickOutside() {
        if (this.menuRef) {
            this.menuRef.closeSubMenus();
        }
    }

    locatePosition() {
        const duringCreation = this.markupsExtension.markupsManager.getDuringCreationMarkup();
        const marginWithMenu = namespace.Constants.CONTEXTUAL_MENU_OFFSET + namespace.Constants.SCREEN_MARGIN;

        if (!duringCreation) {
            this.selectedMarkups = this.markupsExtension.markupsManager
                .getSelectedMarkups()
                .filter(markup => !markup.isGroupMarkup());
            if (this.selectedMarkups.length > 0) {
                this.boundingBox = new THREE.Box3();

                this.selectedMarkups.forEach(markup => {
                    const bbox = markup.getClientBoundingBox(true, true);

                    const eyeDirection = this.markupsExtension.renderer.getCameraEyeVector();
                    GeometryUtils.applyRotation(
                        [bbox.min, bbox.max],
                        this.markupsExtension.renderer.getCameraRotation(),
                        bbox.getCenter(),
                        eyeDirection
                    );
                    bbox.setFromPoints([bbox.min.clone(), bbox.max.clone()]);

                    this.boundingBox.union(bbox);
                });
            } else {
                const selectedLayers = this.markupsExtension.markupsManager.getSelectedLayers();
                const layer = selectedLayers[0];

                if (selectedLayers.length === 1 && !layer.isEmpty()) {
                    this.boundingBox = layer.getClientBoundingBox(true);

                    const eyeDirection = this.markupsExtension.renderer.getCameraEyeVector();
                    GeometryUtils.applyRotation(
                        [this.boundingBox.min, this.boundingBox.max],
                        this.markupsExtension.renderer.getCameraRotation(),
                        this.boundingBox.getCenter(),
                        eyeDirection
                    );
                    this.boundingBox.setFromPoints([this.boundingBox.min.clone(), this.boundingBox.max.clone()]);

                    this.selectedMarkups = layer.getAllMarkups();
                    this.selectedLayer = layer;
                }
            }
        }

        if (!this.boundingBox) {
            return false;
        }

        const { min, max } = this.boundingBox;
        const center = this.boundingBox.getCenter();

        const viewerBounds = this.viewer.impl.getCanvasBoundingClientRect();
        this.viewerWidth = viewerBounds.width;
        const viewerHeight = viewerBounds.height;

        if (!this.isSubMenuVisible) {
            this.subMenuBB = null;
        }

        max.y += this.menuHeight / 2;
        min.y -= this.menuHeight + namespace.Constants.ROTATION_ANCHOR_DISTANCE + namespace.Constants.SCREEN_MARGIN;

        this.clippedXPosition = Math.max(namespace.Constants.SCREEN_MARGIN, center.x - this.menuWidth / 2);
        this.clippedXPosition = Math.min(this.clippedXPosition, this.viewerWidth - (this.menuWidth + marginWithMenu));

        if (!this.position) {
            this.position = new THREE.Vector3();
        }
        this.position.setX(this.clippedXPosition);

        const previousDirection = this.direction;

        const locateOnBottom = () => {
            this.direction = ToolbarLocation.TOP;
        };

        const locateOnTop = () => {
            this.direction = ToolbarLocation.BOTTOM;
        };

        this.position.setY(min.y);

        const minY = this.subMenuPanelHeight ? min.y - this.subMenuPanelHeight : min.y - this.DEFAULT_FIRST_OFFSET;
        if (minY > marginWithMenu) {
            locateOnTop();
        } else {
            locateOnBottom();
        }

        let bottomBorderY = viewerHeight - (this.menuHeight + marginWithMenu);
        bottomBorderY -=
            this.isSubMenuVisible && this.direction === ToolbarLocation.TOP ?
            this.subMenuPanelHeight + namespace.Constants.SCREEN_MARGIN :
            0;
        if (this.position.y > bottomBorderY) {
            this.position.setY(bottomBorderY);
        }

        let upperBorderY = marginWithMenu;
        upperBorderY +=
            this.isSubMenuVisible && this.direction === ToolbarLocation.BOTTOM ?
            this.subMenuPanelHeight + namespace.Constants.SCREEN_MARGIN :
            0;
        if (this.position.y < upperBorderY) {
            this.position.setY(upperBorderY);
        }

        return previousDirection !== this.direction;
    }

    hideMenu() {
        render(null, this.menuContainer);
    }

    onCameraChanged() {
        const directionChanged = this.locatePosition();
        const subMenuChanged = this.locateSubMenu();

        if (directionChanged) {
            this.render();
        } else {
            this.updateMenuPosition();
            if (subMenuChanged) {
                this.locateSubMenu();
                this.render();
            }
        }
    }

    updateMenuPosition() {
        const bbox = this.menuContainer.getBoundingClientRect();
        this.menuWidth = bbox.width;
        this.menuHeight = bbox.height;
        this.locatePosition();
        if (this.position) {
            if (Autodesk.Viewing.isIE11) {
                // Special treatment for IE in order to support tooltips.
                this.menuContainer.style.left = `${Math.round(this.position.x)}px`;
                this.menuContainer.style.top = `${Math.round(this.position.y)}px`;
            } else {
                this.menuContainer.style.transform = `translate(${Math.round(this.position.x)}px, ${Math.round(this.position.y)}px)`;
            }
        }
    }

    render() {
        if (!this.position) {
            return;
        }

        const isSessionOpen = this.markupsExtension.markupsManager.isMarkupSessionOpen();

        // Don't show contextual menu for layers during session
        if (this.selectedLayer && isSessionOpen) {
            this.hideMenu();
            return;
        }

        const isOnlyTextMarkupsSelected = this.isOnlyTextMarkupsSelected();
        const isTextMarkupsSelected = this.isTextMarkupsSelected();
        const isMarkupWithFill = this.isWithFillColor();

        const props = {
            colors : isOnlyTextMarkupsSelected ? this.getTextLineColors() : this.getColors(),
            fillColors: this.getFillColors(),
            textColors: this.getColors(),
            selectedColor: this.getSelectedColor(),
            selectedFillColor: this.getSelectedFillColor(),
            selectedTextColor: this.getSelectedTextColor(),
            onColorChanged: isSessionOpen ? color => {
                isSessionOpen && this.markupsExtension.changeStyleProperty(
                    namespace.Constants.STYLE_PROPERTIES.COLOR,
                    color
                );
            }:(void 0),
            onFillColorChanged: isSessionOpen && isMarkupWithFill ? fillColor => {
                this.markupsExtension.changeStyleProperty(
                    namespace.Constants.STYLE_PROPERTIES.FILL_COLOR,
                    fillColor
                );
            }:(void 0),
            onSizeChanged: (thickness) => {
                this.markupsExtension.changeStyleProperty(
                    namespace.Constants.STYLE_PROPERTIES.THICKNESS,
                    thickness, true
                );
            },
            onOpacityChanged: (opacity) => {
                this.markupsExtension.changeStyleProperty(
                    namespace.Constants.STYLE_PROPERTIES.FILL_OPACITY,
                    opacity / 100, true
                );
            },
            onDelete: () => this.markupsExtension.deleteInEditMarkups(),
            onTextColorChanged: isTextMarkupsSelected ? color => {
                this.markupsExtension.changeStyleProperty(
                    namespace.Constants.STYLE_PROPERTIES.FONT_COLOR,
                    color
                );
            }:(void 0),
            onTextDecreased: isTextMarkupsSelected ? () => {
                this.disableIncrease = false;
                const fontSize = this.getMeanFontSize();
                const newSize = namespace.Constants.FONT_SIZES.reduce((acc, size) => (size < fontSize ? size : acc), void 0);
                if (newSize) {
                    this.markupsExtension.changeStyleProperty(
                        namespace.Constants.STYLE_PROPERTIES.FONT_SIZE,
                        newSize
                    );
                }
            }:(void 0),
            onTextIncreased: isTextMarkupsSelected ? () => {
                this.disableDecrease = false;
                const fontSize = this.getMeanFontSize();
                const newSize = namespace.Constants.FONT_SIZES.find(v => v > fontSize);
                if (newSize) {
                    this.markupsExtension.changeStyleProperty(
                        namespace.Constants.STYLE_PROPERTIES.FONT_SIZE,
                        newSize
                    );
                }
            }:(void 0),
            disableDecreaseText: this.disableDecrease,
            disableIncreaseText: this.disableIncrease,
            opacity: this.getMeanFillOpacity(),
            selectedSize: this.getMeanThickness(),
            location: this.direction,
            maxThickness: namespace.Constants.MAX_THICKNESS,
            viewerBounds: this.viewer.impl.getCanvasBoundingClientRect(),
            componentRef: ref => {
                this.menuRef = ref;
            },
        };
        render(html `<${MarkupsContextualToolbar} ...${props} />`, this.menuContainer);
        this.updateMenuPosition();
    }

    locateSubMenu() {
        this.buttonX = this.relativeX + this.clippedXPosition;

        this.colorPanelLeft = -(this.subMenuPanelWidth / 2) + this.buttonWidth / 2;
        let tempSubLeft = this.colorPanelLeft;
        const rightEdge =
            this.viewerWidth - namespace.Constants.SCREEN_MARGIN - namespace.Constants.CONTEXTUAL_MENU_OFFSET;
        if (this.buttonX < this.subMenuPanelWidth / 2 + namespace.Constants.SCREEN_MARGIN) {
            tempSubLeft = Math.max(this.colorPanelLeft, namespace.Constants.SCREEN_MARGIN - this.buttonX);
        } else if (this.buttonX > rightEdge - this.subMenuPanelWidth / 2 - this.buttonWidth / 2) {
            const offset = rightEdge - (this.subMenuPanelWidth / 2 + this.buttonX) - this.buttonWidth / 2;
            tempSubLeft = Math.min(this.colorPanelLeft, this.colorPanelLeft + offset);
        }

        if (tempSubLeft !== this.subLeft) {
            this.subLeft = tempSubLeft;
            return true;
        } else {
            return false;
        }
    }

    isDisableTextDecrease() {
        return this.selectedMarkups.every(markup => !markup.isTextMarkup());
    }

    isWithFillColor() {
        return this.selectedMarkups.some(markup => markup.isWithFillMarkup());
    }

    isGeometricMarkupSelected() {
        return this.selectedMarkups.some(markup => markup.isGeometricMarkup());
    }

    isTextMarkupsSelected() {
        return this.selectedMarkups.some(markup => markup.isTextMarkup());
    }

    isOnlyTextMarkupsSelected() {
        return this.selectedMarkups.every(markup => markup.type === namespace.Constants.MARKUP_TYPE_CALLOUT);
    }

    onMarkupDirty(event) {
        if (!event.isDuringEdit) {
            this.onSelectionChanged();
        } else {
            this.setFontSizeButtonStatus();
            this.render();
        }
    }

    setFontSizeButtonStatus() {
        const textMarkups = this.getTextMarkups();

        this.disableIncrease =
            textMarkups.length === 0 ?
            false :
            textMarkups.every(markup => {
                const fontSize = markup.getState().style[namespace.Constants.STYLE_PROPERTIES.FONT_SIZE];
                return fontSize >= namespace.Constants.FONT_SIZES[namespace.Constants.FONT_SIZES.length - 1];
            });
        this.disableDecrease =
            textMarkups.length === 0 ?
            false :
            textMarkups.every(markup => {
                const fontSize = markup.getState().style[namespace.Constants.STYLE_PROPERTIES.FONT_SIZE];
                return fontSize <= namespace.Constants.FONT_SIZES[0];
            });
    }

    getTextMarkups() {
        return this.selectedMarkups.filter(markup => {
            return markup.isTextMarkup();
        });
    }

    getColors() {
        return namespace.Constants.DEFAULT_COLORS.map(color => {
            color.label = Autodesk.Viewing.i18n.translate(color.label);
            return color;
        });
    }

    getFillColors() {
        return namespace.Constants.DEFAULT_FILL_COLORS.map(color => {
            color.label = Autodesk.Viewing.i18n.translate(color.label);
            return color;
        });
    }

    getTextLineColors() {
        const colors = this.getFillColors();

        return colors.map(color => {
            const c = {...color };
            if (c.color === namespace.Constants.COLORS.NO_COLOR) {
                c.label = Autodesk.Viewing.i18n.translate('No Border');
            }
            return c;
        });
    }

    getSelectedColor() {
        if (this.selectedMarkups.length === 1) {
            return this.selectedMarkups[0].getState().style[namespace.Constants.STYLE_PROPERTIES.COLOR];
        } else if (this.selectedMarkups.length > 1) {
            if (this.selectedMarkups.length === 0) {
                return null;
            }

            const color = this.selectedMarkups[0].getState().style[namespace.Constants.STYLE_PROPERTIES.COLOR];

            // Only if all the selected markups have the same color, choose it.
            if (
                this.selectedMarkups.every(
                    markup => markup.getState().style[namespace.Constants.STYLE_PROPERTIES.COLOR] === color
                )
            ) {
                return color;
            } else {
                return null;
            }
        }
        return null;
    }

    getSelectedTextColor() {
        if (this.selectedMarkups.length === 1) {
            return this.selectedMarkups[0].getState().style[namespace.Constants.STYLE_PROPERTIES.FONT_COLOR];
        } else if (this.selectedMarkups.length > 1) {
            const textMarkups = this.getTextMarkups();

            if (textMarkups.length === 0) {
                return null;
            }

            const color = textMarkups[0].getState().style[namespace.Constants.STYLE_PROPERTIES.FONT_COLOR];

            // Only if all the selected markups have the same color, choose it.
            if (
                textMarkups.every(
                    markup => markup.getState().style[namespace.Constants.STYLE_PROPERTIES.FONT_COLOR] === color
                )
            ) {
                return color;
            } else {
                return null;
            }
        }
        return null;
    }

    getSelectedFillColor() {
        if (this.selectedMarkups.length === 1) {
            return this.selectedMarkups[0].getState().style[namespace.Constants.STYLE_PROPERTIES.FILL_COLOR];
        } else if (this.selectedMarkups.length > 1) {
            const color = this.selectedMarkups[0].getState().style[namespace.Constants.STYLE_PROPERTIES.FILL_COLOR];

            // Only if all the selected markups have the same color, choose it.
            if (
                this.selectedMarkups.some(
                    markup => markup.getState().style[namespace.Constants.STYLE_PROPERTIES.FILL_COLOR] !== color
                )
            ) {
                return null;
            } else {
                return color;
            }
        }
        return null;
    }

    getMeanFillOpacity() {
        let opacity = 0;

        for (let i = 0; i < this.selectedMarkups.length; i++) {
            const markup = this.selectedMarkups[i];
            opacity += markup.getState().style[namespace.Constants.STYLE_PROPERTIES.FILL_OPACITY] * 100;
        }

        return Math.round(opacity / this.selectedMarkups.length);
    }

    getMeanThickness() {
        let thickness = 0;
        let n = 0;

        for (let i = 0; i < this.selectedMarkups.length; i++) {
            const markup = this.selectedMarkups[i];
            const currentThickness = markup.getState().style[namespace.Constants.STYLE_PROPERTIES.THICKNESS];

            if (currentThickness !== 0) {
                thickness += currentThickness;
                n += 1;
            }
        }

        thickness /= this.markupsExtension.renderer.getNormaStrokeWidth(); // Get back the factor

        return Math.ceil(thickness / Math.max(n, 1));
    }

    getMeanFontSize() {
        let fontSize = 0;
        let n = 0;

        const selectedTextMarkups = this.getTextMarkups();
        if (selectedTextMarkups.length === 0) {
            return 0;
        }

        for (let i = 0; i < selectedTextMarkups.length; i++) {
            const markup = selectedTextMarkups[i];
            const currentFontSize = markup.getState().style[namespace.Constants.STYLE_PROPERTIES.FONT_SIZE];

            if (currentFontSize !== 0) {
                fontSize += currentFontSize;
                n += 1;
            }
        }

        return fontSize / n;
    }

    destroy() {
        this.unregisterEventListeners();

        this.hideMenu();
        this.uiContainer.removeChild(this.menuContainer);
        this.menuContainer = null;

        this.markupsExtension = null;
        this.viewer = null;
        this.options = null;
    }
}