import { html, render, MarkupToolbar } from './UIComponents';
const namespace = AutodeskNamespace('Autodesk.BIM360.Markups');

export default class MarkupsToolbarClass {
	constructor(markupsExtension, uiContainer, viewer) {
		this.markupsExtension = markupsExtension;
		this.uiContainer = uiContainer;
		this.viewer = viewer;
		this.init();
	}

	init() {
		this.selectedTool = null;

		this.toolbarContainer = document.createElement('div');
		this.toolbarContainer.className = 'markup-ui-container';
		this.uiContainer.appendChild(this.toolbarContainer);

		this.registerEventListeners();

		this.renderToolbar();
	}

	changeSelectedTool({ creationType }) {
		this.selectedTool = creationType;
		this.renderToolbar();
	}

	onToolEnabled() {
		this.renderToolbar();
	}

	registerEventListeners() {
		this.changeSelectedToolBinded = this.changeSelectedTool.bind(this);
		this.onClickOutsideBinded = this.onClickOutside.bind(this);
		this.onToolEnabledBinded = this.onToolEnabled.bind(this);

		this.markupsExtension.markupsManager.addEventListener(
			namespace.Events.MARKUP_SESSION_END,
			this.changeSelectedToolBinded
		);
		this.markupsExtension.markupsManager.addEventListener(
			namespace.Events.MARKUP_CREATION_TYPE_CHANGED,
			this.changeSelectedToolBinded
		);
		this.markupsExtension.markupsManager.addEventListener(namespace.Events.BUTTON_DOWN, this.onClickOutsideBinded);
		this.markupsExtension.markupsManager.addEventListener(
			namespace.Events.MARKUP_TOOL_ENABLED_CHANGED,
			this.onToolEnabledBinded
		);
		this.markupsExtension.markupsManager.addEventListener(
			namespace.Events.MARKUP_STYLE_PROPERTY_CHANGED,
			this.onToolEnabledBinded
		);
	}

	unregisterEventListeners() {
		this.markupsExtension.markupsManager.removeEventListener(
			namespace.Events.MARKUP_SESSION_END,
			this.changeSelectedToolBinded
		);
		this.markupsExtension.markupsManager.removeEventListener(
			namespace.Events.MARKUP_CREATION_TYPE_CHANGED,
			this.changeSelectedToolBinded
		);
		this.markupsExtension.markupsManager.removeEventListener(
			namespace.Events.BUTTON_DOWN,
			this.onClickOutsideBinded
		);
		this.markupsExtension.markupsManager.removeEventListener(
			namespace.Events.MARKUP_STYLE_PROPERTY_CHANGED,
			this.onToolEnabledBinded
		);
	}

	getColors() {
		return namespace.Constants.DEFAULT_COLORS.map(color => {
			color.label = Autodesk.Viewing.i18n.translate(color.label);
			return color;
		});
	}

	getFillColors() {
		return namespace.Constants.DEFAULT_FILL_COLORS.map(color => {
			color.label = Autodesk.Viewing.i18n.translate(color.label);
			return color;
		});
	}

	getOpacity() {
		return Math.round(
			this.markupsExtension.getStyleProperty(namespace.Constants.STYLE_PROPERTIES.FILL_OPACITY) * 100
		);
	}

	getSelectedColor() {
		return this.markupsExtension.getStyleProperty(namespace.Constants.STYLE_PROPERTIES.LAST_COLOR);
	}

	getSelectedFillColor() {
		return this.markupsExtension.getStyleProperty(namespace.Constants.STYLE_PROPERTIES.FILL_COLOR);
	}

	getSelectedSize() {
		const thickness = this.markupsExtension.getStyleProperty(namespace.Constants.STYLE_PROPERTIES.THICKNESS);
		const normalStrokeWidth = this.markupsExtension.renderer.getNormaStrokeWidth();
		return Math.round( thickness / normalStrokeWidth);
	}

	onClickOutside() {
		if (this.toolbarRef) {
			this.toolbarRef.closeSubMenus();
		}
	}

	renderToolbar() {
		if (!this.markupsExtension.isToolEnabled()) {
			this.hideMenu();
			return;
		}

		const props = {
            onClick : (type) => {
				if (type !== this.selectedTool) {
					this.markupsExtension.changeMarkupCreationType(type, true);
				}
				this.markupsExtension.deselectAllMarkups();
			},
			selectedTool: this.selectedTool,
			colors: this.getColors(),
			fillColors: this.getFillColors(),
			opacity: this.getOpacity(),
			selectedColor: this.getSelectedColor(),
			selectedFillColor: this.getSelectedFillColor(),
			selectedSize: this.getSelectedSize(),
			maxThickness: namespace.Constants.MAX_THICKNESS,
			onColorChanged: (color) => {
				this.markupsExtension.changeStyleProperty(namespace.Constants.STYLE_PROPERTIES.COLOR, color);
			},
			onSizeChanged: (thickness, submit) => {
				this.markupsExtension.changeStyleProperty(
					namespace.Constants.STYLE_PROPERTIES.THICKNESS,
					thickness,
					!submit
				);
			},
			onFillColorChanged: (fillColor) => {
				this.markupsExtension.changeStyleProperty(
					namespace.Constants.STYLE_PROPERTIES.FILL_COLOR,
					fillColor
				);
			},
			onOpacityChanged: (fillOpacity, submit) => {
				this.markupsExtension.changeStyleProperty(
					namespace.Constants.STYLE_PROPERTIES.FILL_OPACITY,
					fillOpacity / 100,
					!submit
				);
			},
			componentRef: (ref) => {
				this.toolbarRef = ref;
			},
			viewerBounds: this.viewer.impl.getCanvasBoundingClientRect(),
        };
		render(html`<${MarkupToolbar} ...${props}/>`, this.toolbarContainer);
	}

	hideMenu() {
		render(null, this.toolbarContainer);
	}

	destroy() {
		this.unregisterEventListeners();
		this.hideMenu();
		this.uiContainer.removeChild(this.toolbarContainer);
		this.toolbarContainer = null;

		this.uiContainer = null;
		this.markupsExtension = null;
		this.viewer = null;
	}
}
