import { html, render, MarkupsCreationToast } from './UIComponents';

const namespace = AutodeskNamespace('Autodesk.BIM360.Markups');
const HISTORY_CHANGED_EVENT = 'action-manager.history.changed.event';
export default class MarkupsCreationToastClass {
	constructor(markupsExtension, uiContainer, viewer, options) {
		this.markupsExtension = markupsExtension;
		this.uiContainer = uiContainer;
		this.options = options;
		this.init();
	}

	init() {
		this.menuContainer = document.createElement('div');
		this.menuContainer.className = 'markup-ui-container';
		this.uiContainer.appendChild(this.menuContainer);

		this.showBinded = this.show.bind(this);
		this.hideBinded = this.hide.bind(this);
		this.onHistoryChangedBinded = this.onHistoryChanged.bind(this);

		this.markupsExtension.markupsManager.addEventListener(namespace.Events.MARKUP_SESSION_START, this.showBinded);
		this.markupsExtension.markupsManager.addEventListener(namespace.Events.MARKUP_SESSION_END, this.hideBinded);
		this.markupsExtension.markupsManager.addEventListener(namespace.Events.LAYER_READY_TO_SAVE, this.showBinded);
		this.markupsExtension.markupsManager.addEventListener(
			HISTORY_CHANGED_EVENT,
			this.onHistoryChangedBinded
		);
	}

	destroy() {
		this.markupsExtension.markupsManager.removeEventListener(
			namespace.Events.MARKUP_SESSION_START,
			this.showBinded
		);
		this.markupsExtension.markupsManager.removeEventListener(namespace.Events.MARKUP_SESSION_END, this.hideBinded);
		this.markupsExtension.markupsManager.removeEventListener(namespace.Events.LAYER_READY_TO_SAVE, this.showBinded);
		this.markupsExtension.markupsManager.removeEventListener(
			HISTORY_CHANGED_EVENT,
			this.onHistoryChangedBinded
		);

		this.hide();
		this.uiContainer.removeChild(this.menuContainer);
		this.menuContainer = null;

		this.uiContainer = null;
		this.markupsExtension = null;
	}

	onSaveSnapshot() {
		return this.markupsExtension.markupsManager.generateScreenCapture();
	}

	onDiscardSession() {
		this.markupsExtension.endMarkupSession(false);
	}

	onHistoryChanged(event) {
		if (event && event.actionGroup && event.actionGroup[0] && event.actionGroup[0].isDuringEdit) {
			return;
		}

		this.hasUndo = event.hasUndo;
		this.hasRedo = event.hasRedo;

		if (this.visible) {
			this.show();
		}
	}

	undo() {
		this.markupsExtension.undo();
	}

	redo() {
		this.markupsExtension.redo();
	}

	onStatusChanged(status) {
		const activeLayer = this.markupsExtension.markupsManager.getActiveLayer();
		const isNewMarkup = !activeLayer.layerData;

		this.markupsExtension.changeCreationStatusType(status);

		if (!isNewMarkup) {
			activeLayer.layerData.metadata.status = status;
		}

		this.show();
	}

	show() {
		const activeLayer = this.markupsExtension.markupsManager.getActiveLayer();

		if (!activeLayer) {
			return;
		}
		const props = {
			onSaveSnapshot: this.onSaveSnapshot.bind(this),
			onDiscard: this.onDiscardSession.bind(this),
			onUndo: this.undo.bind(this),
			onRedo: this.redo.bind(this),
			hasUndo: this.hasUndo,
			hasRedo: this.hasRedo,
			saveDisabled: !this.markupsExtension.markupsManager.getReadyToSave(),
		};
		
		render( html`<${MarkupsCreationToast} ...${props}/>`, this.menuContainer );

		this.visible = true;
	}

	hide() {
		render(null, this.menuContainer);
		this.visible = false;
	}
}
